import { css } from "emotion";
import { Color } from "reactism/utils";
import { Wrapper } from "reactism/elements";
import { Link } from "Routes";
import { withRouter } from "next/router";

const HeaderTwo = ({ router }) => {
  const baseStyle = css`
    background-color: #fff;
    border-bottom: 2px solid rgba(0, 0, 0, 0.1);
    .wrapper {
      display: grid;
      grid-template-columns: 11.2% 11.2% 11.2% auto auto 12% 12% 12%;
      justify-items: stretch;
      a {
        text-decoration: none;
        display: block;
        padding: 0.8rem 0;
        color: #000;
        /* transition: 0.2s ease; */
        &:hover {
          font-weight: bold;
          color: ${Color.success};
        }
      }
      ul {
        margin: 0;
        padding: 0;
        li {
          list-style: none;
          margin: 0;
          &.active a {
            color: ${Color.success};
            font-weight: bold;
            border-bottom: 3px solid ${Color.success};
          }
        }
      }
      ul:nth-child(1),
      ul:nth-child(2),
      ul:nth-child(3),
      ul:nth-child(4) {
        justify-self: start;
      }
      ul:nth-child(5),
      ul:nth-child(6),
      ul:nth-child(7),
      ul:nth-child(8) {
        justify-self: end;
      }
    }

    @media (max-width: 700px) {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 10;
      .wrapper {
        display: none;
      }
    }
  `;

  const { pathname } = router;

  return (
    <nav className={baseStyle}>
      <Wrapper pl={7} pr={7}>
        <div className="wrapper">
          <ul>
            <li className={pathname === "/home" ? "active" : null}>
              <Link route="/">
                <a>Beranda</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li className={pathname === "/profile" ? "active" : null}>
              <Link route="profil">
                <a>Profil</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li className={pathname === "/tools" ? "active" : null}>
              <Link route="sarana">
                <a>Sarana</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li className={pathname === "/organization" ? "active" : null}>
              <Link route="organisasi">
                <a>Organisasi</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li
              className={
                pathname === "/news" || pathname === "/detail" ? "active" : null
              }
            >
              <Link route="berita">
                <a>Berita</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li className={pathname === "/gallery" ? "active" : null}>
              <Link route="galeri">
                <a>Galeri</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li className={pathname === "/events" ? "active" : null}>
              <Link route="kegiatan">
                <a>Kegiatan</a>
              </Link>
            </li>
          </ul>
          <ul>
            <li className={pathname === "/features" ? "active" : null}>
              <Link route="layanan">
                <a>Layanan</a>
              </Link>
            </li>
          </ul>
        </div>
      </Wrapper>
    </nav>
  );
};

export default withRouter(HeaderTwo);
